@import url(https://fonts.googleapis.com/css?family=Lora|Open+Sans|Roboto+Slab|Source+Sans+Pro);
.styles_Wrapper__QmsU2 {
  display: grid;
  grid-template-columns: 4cm 1fr;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.styles_WrapperTitleBar__1uVIm {
  grid-template-areas:
    "title title"
    "sidebar preview"
    "status status";
  grid-template-rows: 25px 1fr 25px;
}

.styles_WrapperTopBar__3TDGm {
  grid-template-areas:
    "topbar topbar"
    "sidebar preview"
    "status status";
  grid-template-rows: 8mm 1fr 5mm;  
}

.styles_PreviewOuter__aClrK {
  grid-area: preview;
  flex: 1 1 100%;
  overflow: scroll;
}

@media screen {
  .styles_PreviewInner__2lsw- {
    -webkit-transform: var(--previewZoom);
            transform: var(--previewZoom);
  }
}

.styles_SideBar__11KBm {
  grid-area: sidebar;
  display: flex;
  flex: 1 1 100%;
  flex-direction: column;
  /*background-image: linear-gradient(90deg, #ddd, 5%, #f0f0f0, 95%, #ddd);*/
  background-color: #f5f5f5;
  border-right: 1px solid #ccc;
  overflow: clip;
  padding: 2mm;
}

.styles_SideBar__11KBm > * {
  margin: 2px;
  top: auto;
  bottom: auto;
  left: auto;
  right: auto;
}

.styles_StatusBar__3LGjN {
  grid-area: status;
  /*background-image: linear-gradient(0deg, #ddd, 25%, #f0f0f0, 99%, #ddd);*/
  background-color: #f5f5f5;
  border-top: 1px solid #ccc;
  display: flex;
  padding-left: 2mm;
  padding-right: 2mm;
  flex: 1 1 100%;
  align-items: center;
  justify-content: right;
  font-size: 0.9em;
}

.styles_StatusBar__3LGjN > * {
  top: auto;
  bottom: auto;
  left: auto;
  right: auto;
}

.styles_TitleBar__315t0 {
  grid-area: title;
  background-image: -webkit-linear-gradient(bottom, #ddd, 25%, #ededed, 99%, #ddd);
  background-image: linear-gradient(0deg, #ddd, 25%, #ededed, 99%, #ddd);
  display: flex;
  flex: 1 1 100%;
  align-items: center;
  justify-content: center;
  -webkit-user-select: none;
  -webkit-app-region: drag;
}

.styles_TopBar__I1VNN {
  grid-area: topbar;
  background-color: #f5f5f5;
  border-bottom: 1px solid #ccc;
  display: flex;
  flex: 1 1 100%;
  align-items: center;
  justify-content: center;
  font-size: 0.9em;
}

.styles_TopBar__I1VNN > * {
  bottom: auto;
  top: auto;
}

@media print {
  .styles_Wrapper__QmsU2 {
    display: block;
  }

  .styles_PreviewOuter__aClrK {
    overflow: visible;
  }
  
  .styles_SideBar__11KBm {
    display: none;
  }

  .styles_StatusBar__3LGjN {
    display: none;
  }

  .styles_TopBar__I1VNN {
    display: none;
  }

  .styles_TitleBar__315t0 {
    display: none;
  }
}
.styles_SettingsPage__3aIWR {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  flex-direction: column;
  font-size: 9pt;
}


.styles_Modal__2Q7kX {
  position: absolute;
  margin: 2rem;
  border-radius: 0.5rem;
  background-color: #fafafa;
  outline: none;
  padding: 1rem;
  border: 1px solid rgba(0,0,0,0.5);
}

.styles_Overlay__1XeAm {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(100,100,100,0.3);
}

:root {
  --doc-font-family: 'Lora', serif;
  --doc-font-size: 9pt;

  --ui-font-family: 'Open Sans';

  --editColorSolid: rgba(0,180,100,0.5);
  --editColorFaded: rgba(0,200,120,0.05);
}
@page { margin: 0 }
body { margin: 0 }
.sheet {
  margin: 0;
  overflow: hidden;
  position: relative;
  box-sizing: border-box;
  page-break-after: always;
}

/** Paper sizes **/
div.A3               .sheet { width: 297mm; height: 419mm }
div.A3.landscape     .sheet { width: 420mm; height: 296mm }
div.A4               .sheet { width: 210mm; height: 296mm }
div.A4.landscape     .sheet { width: 297mm; height: 209mm }
div.A5               .sheet { width: 148mm; height: 209mm }
div.A5.landscape     .sheet { width: 210mm; height: 147mm }
div.letter           .sheet { width: 216mm; height: 279mm }
div.letter.landscape .sheet { width: 280mm; height: 215mm }
div.legal            .sheet { width: 216mm; height: 356mm }
div.legal.landscape  .sheet { width: 357mm; height: 215mm }

