div.legal.landscape  .sheet { width: 357mm; }
*/

/** Padding area **/
.sheet.padding-5mm { padding: 5mm }
.sheet.padding-10mm { padding: 10mm }
.sheet.padding-15mm { padding: 15mm }
.sheet.padding-20mm { padding: 20mm }
.sheet.padding-25mm { padding: 25mm }

/** For screen preview **/
@media screen {
  html, body { background-color: #aaa; }
  .sheet {
    background: white;
    box-shadow: 0 .5mm 2mm rgba(0,0,0,.3);
    margin: 3mm auto;
  }
}

/** Fix for Chrome issue #273306 **/
@media print {
           div.A3.landscape { width: 420mm }
  div.A3, div.A4.landscape { width: 297mm }
  div.A4, div.A5.landscape { width: 210mm }
  div.A5                    { width: 148mm }
  div.letter, div.legal    { width: 216mm }
  div.letter.landscape      { width: 280mm }
  div.legal.landscape       { width: 357mm }
}

.katex-html {
  white-space: nowrap;
  font-size: 0.85em;
}

blockquote {
  font-style: italic;
}

/* TABLES */

table {
  border-collapse: collapse;
}

th {
  font-weight: bold;
}

th, td {
  border: 1px solid black;
  padding: 0.2em 0.5em;
}

.Document {
  font-family: serif;
  font-family: var(--doc-font-family, serif);
  font-size: 9pt;
  font-size: var(--doc-font-size, 9pt);
}

.AnswerChoices {
  padding-left: 5mm;
}

.AnswerChoice {
  margin: 2mm;
}

.QuestionWrapper {
  display: grid;
  grid-gap: 0.5em;
  margin-bottom: 0.8em;
  page-break-inside: avoid;
  -webkit-column-break-inside: avoid;
          break-inside: avoid;
}

