.QuestionSet > ol {
  padding-left: 5mm;
}

.Heading {
  border-collapse: collapse;
  width: 100%;
}

.Heading * {
  font-size: 12pt;
  font-weight: bold;
}

.HeadingBlank {
  display: inline-block;
  border-bottom: 1px solid black; 
  margin: 0 1mm;
}

.Heading > tbody > tr > * {
  border: none;
}

.Heading > tbody > tr > td {
  padding-bottom: 3mm;
}

.Heading > tbody > tr > td:first {
  text-align: left;
}

.Heading > tbody > tr > td:last-child {
  text-align: right;
  right: 0;
}

.Heading:hover {
  outline: 1px solid var(--editColorSolid);
}

.Editor:hover {
  outline: none;
}

body {
  font-family: var(--ui-font-family), sans-serif;
}

button, input, select {
  border-radius: 4px;
  border-color: #ccc;
  border-width: 1px;
  border-style: solid;
  background-color: white;
  color: black;
  font-family: var(--ui-font-family);
  margin: 1px;
  font-size: 0.9rem;
}

button:hover, input:hover, select:hover {
  color: black;
  border-color: #999;
}

button:focus, input:focus, select:focus {
  outline: none;
}

button:active, input:active, select:active {
  outline: none;
}

button {
  background-image: -webkit-linear-gradient(white, 50%, #f5f5f5);
  background-image: linear-gradient(white, 50%, #f5f5f5);
}

button:hover {
  background-image: -webkit-linear-gradient(white, 50%, #eee);
  background-image: linear-gradient(white, 50%, #eee);
}

button:active {
  color: white;
  border-color: black;
  background-image: -webkit-linear-gradient(bottom, #27f, 50%, #05f);
  background-image: linear-gradient(0deg, #27f, 50%, #05f);
}




html, body {
  height: 100%;
